export const houseList = HouseList.map(houseItem => {
    const res = String(houseItem.roomId).match(/^([1-9]\d?)(\d\d)$/)
    const key = 'H' + res[2]
    houseItem.houseType = houseTypeList[houseItem.house - 1][key]
    houseItem.floor = Number(res[1])
    houseItem.voice = '--'
    return houseItem
})

